.services {
	position: relative;
	margin-bottom: 215px;
	padding-bottom: 319px;
	background-color: var(--color-darker-blue2);

	._layer {
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		user-select: none;
		pointer-events: none;
	}

	&__mineCraftCube {
		position: absolute;
		right: -260px;
		bottom: -150px;
		z-index: 1;
		width: 448px;
		height: 410px;

		&-small {
			position: absolute;
			right: 280px;
			bottom: 0;
			width: 209px;
			height: 137px;
		}

		&-smaller {
			position: absolute;
			bottom: 337px;
			left: -70px;
			width: 174px;
			height: 153px;
		}

		&-tiny {
			position: absolute;
			bottom: 0;
			left: 72px;
			width: 96px;
			height: 77px;
		}
	}

	&__mineCraftCubeTNT {
		&-small {
			position: absolute;
			bottom: 101px;
			left: 537px;
			width: 178px;
			height: 173px;
		}

		&-smaller {
			position: absolute;
			bottom: 50px;
			left: 907px;
			width: 106px;
			height: 103px;
		}
	}

	&__heading-lines {
		position: relative;
	}

	&__heading-line {
		display: flex;
		align-items: center;
		padding: 30px 0;

		._span {
			display: block;
			flex-shrink: 0;
			margin-right: 94px;
			font-size: 50px;
			line-height: 1.3;
			text-transform: uppercase;

			&:last-child {
				margin-right: 0;
			}
		}

		&._line-first {
			position: absolute;
			top: 0;
			left: 50%;
			z-index: 10;
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
			transform: translateX(-50%) rotate(6deg);

			._span {
				color: #9D7F00;
			}
		}

		&._line-second {
			position: absolute;
			top: 0;
			left: 50%;
			padding: 15px 0;
			border-top: 1px solid var(--color-white);
			border-bottom: 1px solid var(--color-white);
			transform: translateX(-50%) rotate(-3deg);

			._span {
				font-weight: 500;
				font-family: $main-font-family;
				background-image: url(../img/services-line-title.png);
				width: 255px;
				height: 90px;
				background-position: center;
				background-size: cover;
				background-repeat: no-repeat;

				@include _1200 {
					width: 155px;
					height: 40px;
				}

				@include _575 {
					width: 85px;
					height: 20px;
					margin-right: 64px;
				}
			}
		}
	}

	&__title {
		max-width: 712px;
		margin-bottom: 64px;
		padding-top: 280px;

		h2 {
			margin-bottom: 0;
			font-size: 55px;
			line-height: 1.2;
			text-transform: uppercase;

			span {
				color: var(--color-purple);
				text-shadow: 0 1px 36px var(--color-purple);
			}
		}

		@include _575 {
			padding-top: 160px;
		}
	}

	&__container {
		max-width: 1544px;
	}

	&__cube-img {
		position: absolute;
		bottom: -164px;
		left: 231px;
		width: 1054px;
		height: 328px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}

	@include _1680 {
		&__title {
			h2 {
				font-size: rem(50px);
			}
		}

		&__mineCraftCube-small {
			display: none;
		}

		&__mineCraftCube-smaller {
			bottom: 167px;
		}

		&__mineCraftCube-tiny {
			left: 32px;
		}
	}

	@include _1536 {
		&__cube-img {
			left: 291px;
			width: 834px;
			height: 258px;
		}

		&__mineCraftCubeTNT-small {
			bottom: 51px;
			left: 517px;
			width: 148px;
			height: 143px;
		}

		&__mineCraftCubeTNT-smaller {
			bottom: 20px;
			left: 847px;
			width: 76px;
			height: 73px;
		}

		&__mineCraftCube-tiny {
			left: 122px;
		}

		&__mineCraftCube {
			right: -200px;
			bottom: -140px;
		}
	}

	@include _1366 {
		&__title {
			h2 {
				font-size: rem(45px);
			}
		}

		&__mineCraftCube {
			right: -160px;
			bottom: -60px;
			width: 348px;
			height: 310px;
		}
	}

	@include _1200 {
		margin-bottom: 225px;
		padding-bottom: 249px;

		&__heading-line {
			._span {
				font-size: 20px;
			}
		}

		&__title {
			max-width: 642px;
			margin-bottom: 44px;
			padding-top: 210px;

			h2 {
				font-size: rem(35px);
			}
		}

		&__cube-img {
			bottom: -79px;
			left: 131px;
			width: 500px;
			height: 158px;
		}

		&__mineCraftCube {
			display: none;
		}

		&__mineCraftCube-tiny {
			left: 2px;
			width: 66px;
			height: 57px;
		}

		&__mineCraftCubeTNT-small {
			left: 417px;
		}
	}

	@include _769 {
		margin-bottom: 185px;

		&__heading-line {
			&._line-first {
				transform: translateX(-50%) rotate(8deg);
			}

			&._line-second {
				transform: translateX(-50%) rotate(-6deg);
			}
		}

		&__mineCraftCube-smaller {
			bottom: 87px;
		}

		&__mineCraftCubeTNT-smaller {
			right: 260px;
			left: auto;
		}

		&__mineCraftCubeTNT-small {
			left: 265px;
			width: 98px;
			height: 93px;
		}

		&__mineCraftCube-tiny {
			left: auto;
			right: 20px;
		}
	}

	@include _740 {
		margin-bottom: 155px;
		padding-bottom: 179px;

		&__cube-img {
			left: 0;
		}

		&__mineCraftCubeTNT-small {
			display: none;
		}

		&__mineCraftCube-smaller,
		&__mineCraftCube-tiny {
			display: none;
		}

		&__mineCraftCubeTNT-smaller {
			display: none;
		}
	}

	@include _480 {
		padding-bottom: 129px;

		&__title {
			h2 {
				font-size: 1.875rem;
			}
		}

		&__heading-line {
			padding: 29px 0;

			._span {
				margin-right: 34px;
			}
		}

		&__title {
			padding-top: 160px;
		}
	}

	@include _360 {
		padding-bottom: 59px;
		margin-bottom: 75px;

		&__cube-img {
			display: none;
		}
	}
}

.accordion {
	display: flex;
	flex-direction: column;

	&__close {
		position: absolute;
		top: 24px;
		right: 0;
		width: 32px;
		height: 32px;
		transition: transform 0.2s ease-in-out;

		._line {
			position: absolute;
			top: 50%;
			left: 50%;
			display: block;
			width: 32px;
			height: 1px;
			background-color: var(--color-purple);
			transform: translate(-50%, -50%);
			transition: background-color var(--main-transition);

			&:nth-child(1) {
				transform: translate(-50%, -50%) rotate(90deg);
			}
		}

		&:focus {
			._line {
				background-color: var(--color-light-blue);
			}

			~ .accordion__item-top .accordion__item-heading {
				color: var(--color-purple);
			}
		}
	}

	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		padding: 24px 0;
		border-top: 1px solid #3b516c;
		cursor: pointer;

		&-top {
			display: flex;
		}

		&-bottom {
			display: none;
			padding-top: 32px;
		}

		&-left {
			width: 40%;
		}

		&-rigth {
			width: 60%;
		}

		&-heading {
			display: block;
			max-width: 336px;
			font-weight: 500;
			font-size: rem(32px);
			line-height: 1.3;
			transition: color var(--main-transition);
		}

		&-legend {
			display: block;
			max-width: 336px;
			font-weight: 200;
			font-size: rem(24px);
			line-height: 1.4;
		}

		&-info {
			ul {
				max-width: 405px;
				margin-bottom: 32px;

				li {
					position: relative;
					margin-bottom: 16px;
					padding-left: 42px;
					color: var(--color-grey);
					font-size: rem(20px);
					line-height: 1.4;

					&:last-child {
						margin-bottom: 0;
					}

					&::before {
						position: absolute;
						top: 0;
						left: 0;
						width: 11px;
						height: 11px;
						background-image: url(../img/accordion-info-line.svg);
						background-repeat: no-repeat;
						background-position: center;
						background-size: contain;
						content: "";
					}
				}
			}
		}

		&-image {
			display: block;
			width: 396px;
			height: 484px;
			object-fit: cover;
			background-color: var(--color-light-blue);
		}

		&._is-open {
			padding-bottom: 40px;
			cursor: default;

			.accordion__item-bottom {
				display: flex;
			}

			.accordion__close {
				transform: rotate(45deg);
			}

			&:hover {
				.accordion__item-heading {
					color: inherit;
				}
			}
		}

		&:nth-last-of-type(1) {
			border-bottom: 1px solid #3b516c;
		}

		&:hover {
			.accordion__item-heading {
				color: var(--color-purple);
			}
		}
	}

	&__button {
		padding: 25px 43px;

		&-icon {
			margin-left: 21px;
		}
	}

	@include _1536 {
		&__item {
			&-image {
				width: 336px;
				height: 394px;
			}

			&-left {
				width: 37%;
			}
		}
	}

	@include _1366 {
		&__item {
			&-heading {
				font-size: rem(24px);
			}

			&-legend {
				font-size: rem(20px);
			}

			&-info {
				ul {
					li {
						font-size: rem(18px);
					}
				}
			}

			&-image {
				width: 286px;
				height: 334px;
			}
		}

		&__button {
			padding: 21px 43px;
		}
	}

	@include _769 {
		&__item-heading {
			max-width: 170px;
		}

		&__item-image {
			width: 236px;
			height: 274px;
		}
	}

	@include _740 {
		&__item-image {
			display: none;
		}

		&__item-top,
		&__item-bottom {
			flex-direction: column;
		}

		&__item-bottom {
			padding-top: 22px;
		}

		&__item-left {
			width: 100%;
			margin-bottom: 1rem;
		}

		&__item-heading {
			max-width: 100%;
		}

		&__close {
			width: 22px;

			._line {
				width: 22px;
			}
		}
	}
}
