.site-form {
	position: relative;
	margin-top: -250px;
	padding-top: 489px;
	padding-bottom: 90px;

	._layer {
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		user-select: none;
		pointer-events: none;
	}

	.scary-hands {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		height: 1822px;
		transform: translateX(-50%);
	}

	&__container {
		max-width: 792px;
	}

	&__title {
		position: relative;
		z-index: 10;
		margin-bottom: 44px;

		h2 {
			margin-bottom: 16px;
			font-size: 50px;
			line-height: 1.2;
			text-align: center;
			text-transform: uppercase;

			span {
				color: var(--color-purple);
				text-shadow: 0 1px 36px var(--color-purple);
			}
		}

		p {
			max-width: 516px;
			margin: 0 auto;
			font-weight: 200;
			font-size: rem(24px);
			line-height: 1.2;
			text-align: center;
		}
	}

	&__form {
		position: relative;
		z-index: 4;
		max-width: 489px;
		margin: 0 auto;
		padding: 36px;
		padding-bottom: 357px;

		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: -1;
			width: 100%;
			height: 1019px;
			background-image: url(../img/form-bg.png);
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
			transform: translate(-50%, -50%);
			content: "";
		}

		.helmet {
			position: absolute;
			bottom: 0;
			left: 50%;
			z-index: -1;
			width: 670px;
			height: 420px;
			transform: translateX(-50%);
		}
	}

	&__label {
		margin-bottom: 1rem;

		&:nth-of-type(2) {
			.site-input {
				background: linear-gradient(180deg, rgba(108, 2, 75, 1) 0%, rgba(138, 4, 84, 1) 30%, rgba(159, 5, 90, 1) 57%);
			}
		}
	}

	&__agreemnt {
		margin-bottom: 32px;
	}

	&__button {
		position: relative;
		width: 100%;
		box-shadow: 0 1px 0 #7cebff;

		&::before {
			position: absolute;
			bottom: -5px;
			left: 0;
			z-index: -1;
			width: 100%;
			height: 100%;
			background-color: #16b6d3;
			border-radius: 11px;
			transition: background-color var(--main-transition);
			content: "";
		}

		&:hover {
			&::before {
				background-color: var(--color-purple);
			}
		}

		&:focus {
			&::before {
				background-color: var(--color-purple);
			}
		}
	}

	@include _1680 {
		padding-top: 499px;

		&__title {
			h2 {
				font-size: rem(45px);
			}

			p {
				max-width: 456px;
				font-size: 1.3rem;
			}
		}

		.scary-hands {
			height: 1522px;
		}
	}

	@include _1536 {
		.helmet {
			bottom: 20px;
			width: 540px;
			height: 340px;
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
		}
	}

	@include _1366 {
		padding-top: 419px;

		&__title {
			h2 {
				font-size: rem(40px);
			}
		}

		&__button {
			padding: 25px;
		}

		.helmet {
			bottom: 70px;
			width: 510px;
			height: 310px;
		}

		.scary-hands {
			height: 1292px;
		}
	}

	@include _1200 {
		padding-top: 319px;

		&__title {
			margin-bottom: 24px;

			h2 {
				font-size: rem(35px);
			}

			p {
				max-width: 336px;
				font-size: 1rem;
			}
		}

		&__form {
			max-width: 419px;
			padding: 30px 30px 307px;
		}

		.scary-hands {
			height: 1082px;
		}

		.helmet {
			width: 450px;
			height: 280px;
		}
	}

	@include _769 {
		padding-top: 299px;

		.scary-hands {
			height: 862px;
		}
	}

	@include _740 {
		&__title {
			h2 {
				font-size: rem(30px);
			}
		}
	}

	@include _500 {
		.scary-hands {
			display: none;
		}
	}

	@include _480 {
		.helmet {
			width: 340px;
			height: 211px;
		}

		&__form {
			padding-bottom: 257px;
			padding-left: 15px;
			padding-right: 15px;
		}
	}

	@include _320 {
		padding-bottom: 20px;
	}
}
