.differences {
	position: relative;
	padding-top: 554px;
	padding-bottom: 110px;
	background-color: var(--color-darker-blue2);
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;

	@include _1440 {
		background-size: cover;
	}

	.digital-palms {
		position: absolute;
		top: -305px;
		left: 0;
		width: 100%;
		height: 989px;
	}

	.dragon {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 1516px;
	}

	.smog {
		position: absolute;
		top: -234px;
		left: 0;
		width: 100%;
		height: 960px;
	}

	.sonic {
		position: absolute;
		top: -30px;
		left: 36px;
		z-index: 1;
		width: 193px;
		height: 155px;
	}

	.DeLoreanDMC12 {
		position: absolute;
		top: -137px;
		left: -36px;
		width: 1415px;
		height: 206px;
	}

	.CrashBandicoot {
		position: absolute;
		top: -119px;
		right: 145px;
		width: 391px;
		height: 216px;
	}

	._layer {
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		user-select: none;
		pointer-events: none;
	}

	&__container {
		max-width: 1544px;
	}

	&__title {
		position: relative;
		z-index: 25;
		max-width: 798px;
		margin: 0 auto;
		margin-bottom: 64px;

		h2 {
			position: relative;
			z-index: 4;
			font-size: 55px;
			line-height: 1.2;
			text-align: center;
			text-transform: uppercase;
		}
	}

	&__list {
		position: relative;
		z-index: 25;
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		gap: 40px;
		row-gap: 87px;
	}

	&__item {
		display: flex;
		flex-direction: column;
		grid-column: 3 span;
		align-items: center;
		justify-content: center;
		height: 336px;
		padding: 50px 15px;
		background-image: url(../img/polygon.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;

		&-img {
			display: block;
			width: 98px;
			height: 98px;
			margin-bottom: 23px;
			object-fit: contain;
		}

		&-legend {
			display: block;
			margin-bottom: 10px;
			font-weight: 500;
			font-size: rem(24px);
			line-height: 1.4;
			text-align: center;
		}

		&-text {
			display: block;
			max-width: 222px;
			color: var(--color-grey);
			font-weight: 400;
			font-size: rem(20px);
			line-height: 1.4;
			text-align: center;
		}

		p {
			font-size: rem(24px);
			line-height: 1.7;
		}

		&:first-child {
			grid-column: 6 span;
			background-image: none;
		}
	}

	@include _1680 {
		padding-top: 494px;

		.digital-palms {
			top: -257px;
			height: 849px;
		}

		.sonic {
			width: 153px;
			height: 115px;
		}

		.DeLoreanDMC12 {
			width: 1285px;
			height: 186px;
		}

		.CrashBandicoot {
			top: -79px;
			right: 115px;
			width: 271px;
			height: 156px;
		}

		.smog {
			top: -184px;
			height: 750px;
		}

		.dragon {
			height: 1286px;
		}

		&__title {
			max-width: 738px;

			h2 {
				font-size: rem(50px);
			}
		}
	}

	@include _1536 {
		padding-bottom: 160px;

		&__list {
			row-gap: 67px;
		}

		&__item {
			height: 312px;
			background-size: contain;

			p {
				margin: 0;
				font-size: 1.3rem;
			}

			&-img {
				width: 88px;
				height: 88px;
				margin-bottom: 18px;
			}

			&-legend {
				font-size: 1.4rem;
			}

			&-text {
				font-size: 1rem;
			}
		}
	}

	@include _1366 {
		padding-top: 414px;

		.digital-palms {
			height: 685px;
		}

		.CrashBandicoot {
			top: -99px;
			right: 65px;
			width: 231px;
			height: 126px;
		}

		.DeLoreanDMC12 {
			width: 885px;
			height: 126px;
		}

		.sonic {
			top: -50px;
			width: 123px;
			height: 95px;
		}

		.smog {
			height: 530px;
		}

		.dragon {
			top: -50px;
			height: 1036px;
		}

		&__title {
			max-width: 628px;

			h2 {
				margin-bottom: 0;
				font-size: rem(45px);
			}
		}

		&__list {
			row-gap: 37px;
		}

		&__item {
			background-size: 100%;
		}
	}

	@include _1200 {
		background-position: 100% 15%;
		background-size: contain;

		.digital-palms {
			top: -180px;
			height: 525px;
		}

		.DeLoreanDMC12 {
			top: -97px;
			left: 0;
			width: 715px;
			height: 96px;
		}

		.sonic {
			top: -20px;
			width: 103px;
			height: 85px;
		}

		.CrashBandicoot {
			top: -79px;
			right: 65px;
			width: 181px;
			height: 96px;
		}

		.dragon {
			top: 0;
			height: 786px;
		}

		.smog {
			top: -84px;
			height: 430px;
		}

		&__title {
			max-width: 488px;
			margin-bottom: 44px;

			h2 {
				font-size: rem(35px);
			}
		}

		&__item {
			grid-column: 4 span;
			height: 288px;
			padding: 20px 15px;
			background-size: contain;

			&-img {
				width: 68px;
				height: 68px;
			}

			&:first-child {
				grid-column: 12 span;
				height: auto;
				padding: 22px 0;
				text-align: center;
			}
		}
	}

	@include _769 {
		&__list {
			gap: 20px;
			row-gap: 30px;
		}

		&__item {
			grid-column: 6 span;
			height: 295px;
		}
	}

	@include _769 {
		padding-top: 224px;
		background-position: 100% 5%;

		.CrashBandicoot {
			display: none;
		}

		.digital-palms {
			top: -110px;
			height: 355px;
		}

		.DeLoreanDMC12 {
			top: -57px;
			left: -50px;
			width: 535px;
			height: 77px;
		}

		.sonic {
			top: 10px;
			width: 83px;
			height: 65px;
		}

		.dragon {
			height: 596px;
		}

		&__title {
			max-width: 438px;
			margin-bottom: 19px;

			h2 {
				font-size: rem(30px);
			}
		}
	}

	@include _740 {
		padding-bottom: 80px;

		&__item {
			background-image: none;
			height: auto;
			justify-content: stretch;
		}
	}

	@include _500 {
		padding-top: 144px;
		background-position: 100% 0%;

		.digital-palms {
			display: none;
		}

		.sonic {
			display: none;
		}

		.DeLoreanDMC12 {
			display: none;
		}

		.smog {
			top: -124px;
			height: 250px;
		}

		.dragon {
			top: -100px;
			height: 396px;
		}
	}

	@include _480 {
		&__item {
			padding: 15px 4px;

			p {
				font-size: 1rem;
			}

			&-img {
				width: 48px;
				height: 48px;
			}

			&-legend {
				line-height: 1.2;
			}
		}

		&__list {
			row-gap: 10px;
		}
	}
}
