.hero {
	position: relative;
	z-index: 10;
	padding-top: 384px;
	padding-bottom: 160px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;

	&::before {
		position: absolute;
		bottom: 0;
		z-index: 10;
		width: 100%;
		height: 100px;
		background: linear-gradient(0deg, rgba($color: #0a2547, $alpha: 1) 30%, transparent 90%);
		content: "";
	}

	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 5;
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		user-select: none;
		pointer-events: none;

		&-stars {
			position: absolute;
			top: 0;
			left: 0;
			display: none;
			width: 100%;
			height: 100%;
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			user-select: none;
			pointer-events: none;
		}
	}

	&__matrix-container {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 60%;
		height: 700px;
		user-select: none;
		pointer-events: none;
	}

	&__container {
		h1 {
			position: relative;
			z-index: 5;
			max-width: 945px;
			margin-bottom: 152px;
			padding-left: 50px;
			font-family: $second-font-family;
			font-size: 72px;
			line-height: 1;
			text-transform: uppercase;
			text-shadow: 0 1px 36px rgba(255, 255, 255, 0.6);

			span {
				color: var(--color-purple);
				text-shadow: none;
			}

			&::before {
				position: absolute;
				top: 35px;
				left: 0;
				width: 32px;
				height: 11px;
				background-image: url(../img/right-arrow.svg);
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				content: "";
			}
		}

		ul {
			position: relative;
			z-index: 15;
			display: grid;
			grid-template-columns: repeat(12, 1fr);
			max-width: 411px;
			margin-bottom: 64px;
			margin-left: 352px;
			column-gap: 42px;
			row-gap: 64px;

			li {
				position: relative;
				grid-column: 6 span;
				max-width: 189px;
				min-height: 71px;
				padding-top: 12px;
				padding-left: 19px;
				font-weight: 200;
				font-size: 20px;
				line-height: 1.5;

				span {
					display: block;
					font-weight: 500;
				}

				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 32px;
					height: 1px;
					background-color: var(--color-purple);
					content: "";
				}

				&::after {
					position: absolute;
					top: 0;
					left: 0;
					width: 1px;
					height: 32px;
					background-color: var(--color-purple);
					content: "";
				}
			}
		}
	}

	&__subtitle {
		position: relative;
		z-index: 5;
		display: block;
		max-width: 253px;
		margin-bottom: 14px;
		font-weight: 200;
		font-size: 24px;
		line-height: 1.3;

		&::before {
			position: absolute;
			top: 90%;
			left: 50%;
			z-index: -1;
			width: 367px;
			height: 367px;
			border: 1px solid var(--color-white);
			border-radius: 50%;
			transform: translate(-50%, -50%);
			opacity: 0.2;
			content: "";
		}
	}

	&__button {
		margin-left: 352px;
	}

	._layer {
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		user-select: none;
		pointer-events: none;
	}

	&__rock {
		position: absolute;
		bottom: -32px;
		left: 0;
		z-index: 5;
		width: 196px;
		height: 583px;

		&._small {
			top: 160px;
			right: 0;
			bottom: auto;
			left: auto;
			z-index: 5;
			width: 110px;
			height: 269px;
		}
	}

	&__plane {
		position: absolute;
		top: 118px;
		left: 292px;
		z-index: 5;
		width: 214px;
		height: 192px;
	}

	@include _1680 {
		padding-top: 274px;

		&__subtitle {
			max-width: 243px;
			font-size: rem(20px);

			&::before {
				top: 60%;
				left: 40%;
				width: 307px;
				height: 307px;
			}
		}

		&__container {
			h1 {
				margin-bottom: 110px;
				font-size: rem(62px);

				&::before {
					top: 22px;
				}
			}

			ul {
				margin-left: 230px;
			}
		}

		&__button {
			margin-left: 230px;
		}

		&__rock {
			bottom: 18px;
			width: 156px;
			height: 463px;

			&._small {
				width: 90px;
				height: 219px;
			}
		}

		&__plane {
			top: 138px;
			left: 282px;
			width: 154px;
			height: 132px;
		}
	}

	@include _1536 {
		padding-bottom: 140px;

		&__rock {
			display: none;

			&._small {
				display: block;
			}
		}

		&__container {

			ul {
				margin-left: 50px;
			}
		}

		&__button {
			margin-left: 50px;
		}
	}

	@include _1366 {
		&__container {
			h1 {
				margin-bottom: 70px;
				font-size: rem(50px);
			}

			ul {
				column-gap: 38px;
				row-gap: 45px;

				li {
					font-size: 1.1rem;
				}
			}
		}

		&__subtitle {
			max-width: 183px;
			font-size: 1rem;

			&::before {
				width: 247px;
				height: 247px;
			}
		}

		&__plane {
			top: 118px;
			left: 212px;
			width: 124px;
			height: 102px;
		}
	}

	@include _1200 {
		padding-top: 214px;

		&__bg {
			z-index: 5;
			background-image: url(../img/hero-mobile.webp) !important;

			&-stars {
				display: block;
			}
		}

		&__plane {
			display: none;
		}

		&__container {
			h1 {
				max-width: 675px;
				padding-left: 0;
				font-size: rem(40px);

				&::before {
					display: none;
				}
			}

			ul {
				margin-left: 0;
			}
		}

		&__button {
			margin-left: 0;
		}
	}

	@include _769 {
		&__rock {
			&._small {
				display: none;
			}
		}
	}

	@include _740 {
		padding-bottom: 90px;

		&__container {
			h1 {
				margin-bottom: 40px;
			}

			ul {
				column-gap: 18px;
				row-gap: 25px;

				li {
					z-index: 10;
				}
			}
		}
	}

	@include _500 {
		padding-top: 184px;

		&__container {
			h1 {
				font-size: rem(35px);
			}
		}
	}

	@include _480 {
		padding-top: 120px;
		padding-bottom: 50px;

		&::before {
			display: none;
		}

		&__bg {
			top: auto;
			bottom: 170px;
			right: -20px;
			left: auto;
			width: 101%;
			height: 50%;
		}

		&__subtitle {
			&::before {
				display: none;
			}
		}

		&__container {
			h1 {
				br {
					display: none;
				}
			}

			ul {
				column-gap: 0;
				row-gap: 7px;
				margin-bottom: 24px;

				li {
					grid-column: 12 span;
				}
			}
		}

		&__button {
			font-size: 1rem;

			&::before,
			&::after {
				top: 5px;
				height: 79px;
			}
		}
	}

	@include _360 {
		&__container {
			h1 {
				font-size: 2rem;
			}
		}

		&__subtitle {
			max-width: 153px;
		}
	}
}
