.section-price {
	position: relative;
	margin-bottom: 160px;

	._layer {
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		user-select: none;
		pointer-events: none;
	}

	&__tentacles {
		position: absolute;
		top: -100px;
		right: 0;
		z-index: 1;
		width: 447px;
		height: 986px;
	}

	&__sharkkdodododo {
		position: absolute;
		right: 101px;
		bottom: -130px;
		width: 651px;
		height: 351px;
	}

	&__diver {
		position: absolute;
		bottom: 50px;
		left: 391px;
		width: 316px;
		height: 141px;
		transform: rotate(17deg);
	}

	&__bomb {
		position: absolute;
		bottom: -220px;
		left: -90px;
		width: 305px;
		height: 450px;
	}

	&__mineCraftCube {
		position: absolute;
		top: -150px;
		left: -310px;
		z-index: 1;
		width: 448px;
		height: 410px;
	}

	&__title {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		max-width: 380px;
		height: 114px;
		margin: 0 auto;
		margin-bottom: 38px;
		text-align: center;

		._titleIcon {
			position: absolute;
			top: 0;
			display: block;
			color: var(--color-purple);
			font-weight: 200;
			font-size: 114px;
			line-height: 1;
			transform: translateY(-4px);
		}

		._left-icon {
			left: 0;
		}

		._right-icon {
			right: 0;
		}

		h2 {
			margin: 0;
			font-size: 55px;
			line-height: 1.2;
			text-transform: uppercase;
		}
	}

	&__list {
		display: flex;
		margin-bottom: 64px;
		border-top: 1px solid #3b516c;
		border-bottom: 1px solid #3b516c;
	}

	&__button {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		padding: 32px;
		transform: translateY(100%);
		transform-origin: bottom;
		transition: transform 0.2s ease-in-out, color var(--main-transition);

		&-icon {
			margin-left: 27px;
		}

		&:focus {
			transform: translateY(0);
		}
	}

	&__item {
		position: relative;
		width: 25%;
		min-height: 476px;
		padding: 64px 80px;
		overflow: hidden;
		text-align: center;
		border-right: 1px solid #3b516c;

		&:nth-of-type(1) {
			border-left: 1px solid #3b516c;
		}

		&:hover {
			.section-price__button {
				transform: translateY(0);
			}
		}
	}

	&__item-head {
		display: block;
		margin-bottom: 40px;
		font-weight: 500;
		font-size: rem(32px);
		line-height: 1.4;
	}

	&__item-price {
		display: block;
		max-width: 234px;
		margin: 0 auto;
		margin-bottom: 19px;
		font-size: 20px;
		line-height: 1.4;

		&:nth-last-of-type(1) {
			margin-bottom: 0;
		}
	}

	&__text {
		max-width: 355px;
		margin: 0 auto;

		p {
			margin: 0;
			margin-bottom: 1rem;
			color: var(--color-grey);
			font-size: 20px;
			line-height: 1.4;
			text-align: center;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	@include _1680 {
		&__title {
			max-width: 370px;
			height: 134px;

			h2 {
				font-size: rem(50px);
			}
		}

		&__bomb {
			bottom: -120px;
			left: -40px;
			width: 215px;
			height: 320px;
		}

		&__diver {
			width: 266px;
			height: 101px;
		}

		&__sharkkdodododo {
			right: 71px;
			bottom: -100px;
			z-index: -1;
			width: 531px;
			height: 271px;
		}

		&__tentacles {
			top: -22px;
			width: 367px;
			height: 816px;
		}

		&__item {
			min-height: 406px;
			padding: 44px 60px;
		}
	}

	@include _1536 {
		&__item {
			min-height: 386px;
		}

		&__item-head {
			font-size: rem(26px);
		}

		&__item-price {
			font-size: rem(18px);
		}

		&__button {
			padding: 23px;
		}

		&__diver {
			left: 251px;
		}

		&__text {
			position: relative;
			z-index: 1;
		}

		&__sharkkdodododo {
			right: 51px;
		}
	}

	@include _1366 {
		&__mineCraftCube {
			top: -70px;
			left: -220px;
			width: 328px;
			height: 310px;
		}

		&__list {
			margin-bottom: 44px;
		}

		&__title {
			h2 {
				font-size: rem(45px);
			}
		}

		&__item {
			padding: 38px 30px;
		}

		&__item-head {
			margin-bottom: 25px;
			line-height: 1.2;
		}

		&__bomb {
			display: none;
		}

		&__diver {
			left: 71px;
		}

		&__sharkkdodododo {
			right: -19px;
		}

		&__text {
			p {
				font-size: 1rem;
			}
		}
	}

	@include _1200 {
		margin-bottom: 90px;

		&__title {
			max-width: 240px;
			height: auto;

			h2 {
				font-size: rem(35px);
			}

			._titleIcon {
				font-size: 70px;
			}
		}

		&__mineCraftCube {
			top: -170px;
		}

		&__tentacles {
			display: none;
		}

		&__button {
			position: static;
			bottom: auto;
			left: auto;
			display: inline-flex;
			width: auto;
			margin-top: auto;
			transform: none;
		}

		&__list {
			flex-wrap: wrap;
		}

		&__item {
			display: flex;
			flex-direction: column;
			width: 50%;

			&:nth-child(1) {
				border-bottom: 1px solid #3b516c;
			}

			&:nth-child(2) {
				border-bottom: 1px solid #3b516c;
			}

			&:nth-child(3) {
				border-left: 1px solid #3b516c;
			}
		}

		&__item-price {
			&:nth-last-of-type(1) {
				margin-bottom: 32px;
			}
		}

		&__sharkkdodododo {
			bottom: -10px;
			width: 391px;
			height: 211px;
		}

		&__diver {
			width: 226px;
			height: 101px;
		}
	}

	@include _769 {
		&__mineCraftCube {
			display: none;
		}

		&__diver {
			left: 21px;
			width: 196px;
			height: 81px;
		}

		&__sharkkdodododo {
			right: -119px;
			width: 341px;
			height: 171px;
		}
	}

	@include _740 {
		&__title {
			max-width: 220px;

			h2 {
				font-size: rem(30px);
			}

			._titleIcon {
				font-size: 50px;
			}
		}

		&__item {
			min-height: 356px;
			padding: 28px 20px;
		}

		&__item-head {
			font-size: rem(20px);
		}

		&__item-price {
			font-size: 1rem;
		}

		&__button {
			padding: 19px  23px;
		}
	}

	@include _480 {
		margin-bottom: 120px;

		&__sharkkdodododo {
			bottom: -150px;
			width: 281px;
			height: 141px;
		}

		&__diver {
			width: 146px;
			height: 61px;
			bottom: -80px;
		}

		&__list {
			margin-bottom: 34px;
			margin-left: -20px;
			margin-right: -20px;
		}

		&__item {
			width: 100%;
			border: none;

			&:nth-of-type(1) {
				border-left: none;
			}

			&:nth-of-type(3) {
				border-left: none;
				border-bottom: 1px solid #3b516c;
			}
		}

		&__text {
			max-width: 315px;
		}
	}

	@include _360 {
		&__title {
			max-width: 190px;

			._titleIcon {
				font-size: 40px;
			}
		}

		&__item {
			min-height: 316px;

			&-price {
				max-width: 194px;
			}
		}

		&__button {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
		}
	}
}
