.hot-stock {
	margin-bottom: 43px;

	&__container {
		max-width: 1544px;
	}

	&__title {
		max-width: 526px;
		margin-bottom: 50px;

		h2 {
			margin-bottom: 0;
			font-size: 55px;
			line-height: 1.2;
			text-transform: uppercase;

			span {
				color: var(--color-purple);
			}
		}
	}

	&__slider-nav {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 5;
		display: flex;
		flex-direction: column;

		&._mobile {
			display: none;
		}
	}

	@include _1680 {
		&__title {
			h2 {
				font-size: rem(50px);
			}
		}
	}

	@include _1366 {
		&__title {
			h2 {
				font-size: rem(45px);
			}
		}
	}

	@include _1200 {
		margin-bottom: 113px;

		&__slider-nav {
			position: static;
			top: auto;
			left: auto;
			display: none;
			flex-direction: row;
			justify-content: space-between;
			width: 100%;
			max-width: 202px;

			&._mobile {
				display: flex;
			}
		}

		&__title {
			max-width: 416px;
			margin-bottom: 0;

			h2 {
				font-size: rem(35px);

				br {
					display: none;
				}
			}
		}

		&__title_wrap {
			display: flex;
			align-items: flex-end;
			justify-content: space-between;
			margin-bottom: 40px;
		}
	}

	@include _992 {
		&__slider-nav {
			&._mobile {
				max-width: 162px;
			}
		}
	}

	@include _740 {
		margin-bottom: 83px;

		&__title {
			h2 {
				font-size: rem(32px);
			}
		}
	}

	@include _500 {
		&__title_wrap {
			flex-direction: column;
			align-items: stretch;
		}

		&__title {
			max-width: 356px;
			margin-bottom: 24px;
		}
	}

	@include _360 {
		margin-bottom: 63px;

		&__title {
			h2 {
				font-size: 1.9rem;
			}
		}
	}
}

.hot-stock-slider {
	position: relative;

	&__nav-button {
		&._right {
			margin-bottom: 24px;
		}
	}

	&__image {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		user-select: none;
		pointer-events: none;
	}

	&__item {
		position: relative;
		z-index: 10;
		width: 100%;
		max-width: 1276px;
		padding: 92px 54px;
		padding-bottom: 206px;
		background-color: var(--color-dark-blue2);
		transition: transform 0.2s ease-in-out;

		&::before {
			position: absolute;
			right: 0;
			bottom: 0;
			z-index: 15;
			display: inline-block;
			width: 0;
			height: 0;
			border-color: transparent transparent var(--color-dark-blue) transparent;
			border-style: solid;
			border-width: 0 0 165px 165px;
			content: "";
		}

		&::after {
			position: absolute;
			right: 0;
			bottom: 0;
			z-index: 15;
			width: 145px;
			height: 145px;
			background-image: url(../img/triangle.png);
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			content: "";
		}
	}

	&__heading {
		max-width: 236px;
		margin-bottom: 1rem;
		font-weight: 500;
		font-size: 32px;
		font-family: $main-font-family;
		line-height: 1.3;

		&::before {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 50;
			display: inline-block;
			width: 0;
			height: 0;
			border-color: var(--color-dark-blue) transparent transparent transparent;
			border-style: solid;
			border-width: 30px 30px 0 0;
			content: "";
		}
	}

	&__text {
		display: block;
		max-width: 236px;
		margin-bottom: 102px;
		font-weight: 200;
		font-size: 20px;
		line-height: 1.4;
	}

	&__button {
		position: relative;
		z-index: 5;
		color: var(--color-white);
		font-weight: 500;
		font-size: 18px;
		line-height: 1.5;
		text-transform: uppercase;

		&::after {
			position: absolute;
			top: 50%;
			right: -44px;
			z-index: -1;
			width: 166px;
			height: 166px;
			background-color: var(--color-red);
			border-radius: 50%;
			box-shadow: inset 0 0 30px #e67b53;
			transform: translateY(-50%) scale(0);
			opacity: 0;
			transition: opacity 0.4s ease-in-out, transform 0.4s ease-in-out;
			content: "";
		}

		&:hover {
			&::after {
				transform: translateY(-50%) scale(1);
				opacity: 1;
			}
		}

		&:focus {
			&::after {
				transform: translateY(-50%) scale(1);
				opacity: 1;
			}
		}
	}

	.swiper-slide {
		position: relative;

		.hot-stock-slider__image,
		.hot-stock-slider__heading,
		.hot-stock-slider__text,
		.hot-stock-slider__button {
			opacity: 0;
		}

		&.swiper-slide-active {
			display: flex;
			justify-content: flex-end;

			.hot-stock-slider__item {
				transform: translateY(-85px);
			}

			.hot-stock-slider__image,
			.hot-stock-slider__heading,
			.hot-stock-slider__text,
			.hot-stock-slider__button {
				opacity: 1;
			}
		}

		&.swiper-slide-prev {
			display: flex;
			justify-content: flex-end;
		}
	}

	@include _1680 {
		&__item {
			padding: 72px 44px 166px;
		}
	}

	@include _1440 {
		&__item {
			max-width: 1190px;
		}
	}

	@include _1366 {
		&__item {
			max-width: 1070px;
			padding: 72px 44px 96px;
		}
	}

	@include _1200 {
		&__heading {
			font-size: rem(24px);
		}

		&__text {
			margin-bottom: 72px;
			font-size: rem(18px);
		}

		&__button {
			&::after {
				width: 136px;
				height: 136px;
				transform: translateY(-50%);
				opacity: 1;
			}
		}

		.swiper-slide.swiper-slide-active {
			.hot-stock-slider__item {
				transform: none;
			}
		}

		&__nav-button {
			&._right {
				order: 2;
				margin-bottom: 0;
			}
		}
	}

	@include _992 {
		&__item {
			padding: 52px 24px 76px;

			&::before {
				border-width: 0 0 115px 115px;
			}

			&::after {
				width: 95px;
				height: 95px;
			}
		}

		&__heading {
			max-width: 176px;
		}

		&__text {
			max-width: 214px;
		}
	}

	@include _740 {
		&__item {
			padding: 32px 24px 66px;
		}

		&__heading {
			max-width: 286px;
			font-size: 1.2rem;
		}

		&__text {
			max-width: 184px;
			font-size: 1rem;
		}

		&__button {
			&::after {
				right: -24px;
				width: 96px;
				height: 96px;
			}
		}
	}

	@include _500 {
		&__item {
			&::before {
				display: none;
			}

			&::after {
				display: none;
			}
		}

		&__heading {
			&::before {
				display: none;
			}
		}

		&__button {
			font-size: 17px;
		}
	}

	@include _480 {
		width: auto;
		margin-left: -20px;
		margin-right: -20px;
	}
}
