.site-footer {
	position: relative;
	z-index: 10;

	&__container {
		display: flex;
		max-width: 1544px;
		padding-bottom: 94px;
	}

	&__bg {
		position: absolute;
		bottom: 0;
		left: 50%;
		z-index: -1;
		transform: translateX(-50%);
		user-select: none;
		pointer-events: none;
	}

	&__col {
		width: 100%;
		max-width: 307px;

		&:nth-of-type(1) {
			margin-right: 70px;
		}

		&:nth-of-type(2) {
			max-width: 300px;
			margin-right: 76px;
		}

		&:nth-last-of-type(1) {
			margin-left: auto;
			text-align: right;
		}
	}

	&__logo {
		margin-bottom: 28px;

		&:hover {
			.site-logo__text {
				color: var(--color-light-blue);
			}
		}

		&:focus {
			.site-logo__text {
				color: var(--color-light-blue);
			}
		}
	}

	&__text {
		display: block;
		font-weight: 500;
		font-size: rem(20px);
		line-height: 1.3;
	}

	&__legeng {
		display: block;
		margin-bottom: 20px;
		font-weight: 500;
		font-size: rem(32px);
		line-height: 1.2;
	}

	&__adress {
		display: block;
		font-weight: 500;
		font-size: rem(20px);
		line-height: 1.5;
	}

	&__subscribe {
		display: block;
		margin-bottom: 24px;
		font-weight: 200;
		font-size: 1rem;
		line-height: 1.3;
	}

	&__button {
		display: inline-block;
	}

	&__link {
		position: relative;
		z-index: 5;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 38px;
		height: 38px;

		&::after {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: -1;
			width: 100%;
			height: 100%;
			background: linear-gradient(45deg, var(--color-light-blue), var(--color-purple));
			border-radius: 9px;
			transform: translate(-50%, -50%);
			content: "";
		}

		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: -1;
			width: 100%;
			height: 100%;
			background: linear-gradient(45deg, var(--color-light-blue), var(--color-purple));
			transform: translate(-50%, -50%);
			filter: blur(10px);
			transition: opacity var(--main-transition);
			content: "";
		}

		&:hover {
			&::before {
				opacity: 0;
			}
		}

		&:focus {
			&::before {
				opacity: 0;
			}
		}
	}

	&__link-icon {
		width: 29px;
		height: 29px;
		fill: var(--color-white);
	}

	.phone-content {
		margin-bottom: 14px;
		font-size: 24px !important;
		line-height: 1 !important;
		transition: color var(--main-transition);

		&:hover {
			color: var(--color-light-blue) !important;
		}

		&:focus {
			color: var(--color-light-blue) !important;
		}
	}

	@include _1536 {
		&__container {
			padding-bottom: 60px;
		}

		&__legeng {
			font-size: rem(24px);
		}

		&__adress {
			font-size: rem(16px);
		}

		&__text {
			font-size: 1rem;
		}

		.phone-content {
			font-size: rem(20px) !important;
		}
	}

	@include _1366 {
		&__col {
			&:nth-of-type(1) {
				margin-right: 50px;
			}
		}
	}

	@include _1280 {
		&__col {
			&:nth-of-type(2) {
				max-width: 180px;
			}
		}

		&__legeng {
			font-size: rem(20px);
		}

		.phone-content {
			font-size: rem(18px) !important;
		}
	}

	@include _1200 {
		&__container {
			flex-wrap: wrap;
			padding-bottom: 40px;
		}

		&__logo {
			margin-bottom: 0;
		}

		&__text {
			display: none;
		}

		&__legeng {
			margin-bottom: 10px;
		}

		&__col {
			&:nth-of-type(1) {
				order: 1;
				width: 50%;
				max-width: 50%;
				margin-right: 0;
				margin-bottom: 32px;
			}

			&:nth-of-type(2) {
				order: 4;
				width: 50%;
				max-width: 50%;
				margin-right: 0;
				text-align: right;
			}

			&:nth-of-type(3) {
				order: 3;
				width: 50%;
				max-width: 50%;
				margin-right: 0;
			}

			&:nth-last-of-type(1) {
				display: flex;
				flex-direction: column;
				align-items: flex-end;
				order: 2;
				width: 50%;
				max-width: 50%;
				margin-left: 0;
			}
		}
	}

	@include _740 {
		&__logo {
			display: inline-block;
		}

		&__col {
			&:nth-of-type(1) {
				width: 100%;
				max-width: 100%;
			}

			&:nth-of-type(2) {
				order: 3;
				text-align: left;
			}

			&:nth-last-of-type(1) {
				align-items: flex-start;
				width: 100%;
				max-width: 100%;
				margin-bottom: 32px;
			}
		}
	}

	@include _575 {
		&__col {
			&:nth-of-type(2) {
				width: 100%;
				max-width: 100%;
				margin-bottom: 32px;
			}

			&:nth-of-type(3) {
				width: 100%;
				max-width: 100%;
				margin-bottom: 32px;
			}
		}

		&__subscribe {
			margin-bottom: 16px;
		}
	}

	@include _500 {
		&__container {
			padding-bottom: 10px;
		}
	}

	@include _480 {
		&__col {
			&:nth-of-type(1) {
				margin-bottom: 24px;
			}

			&:nth-of-type(2) {
				margin-bottom: 24px;
			}

			&:nth-of-type(3) {
				margin-bottom: 24px;
			}
		}
	}
}
