.site-header {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 300;
	width: 100%;
	padding-top: 26px;

	&__container {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-right: 253px;

		@include _1440 {
			padding-right: 253px;
		}

		@include _1200 {
			padding-right: 234px;
		}

		@include _992 {
			padding-right: 24px;
		}
	}

	&__links {
		max-width: 153px;
		text-align: right;

		.phone-content {
			margin-bottom: 11px;

			@include _480 {
				margin-bottom: 7px;
			}

			&:focus {
				color: var(--color-purple) !important;
			}

			&:hover {
				color: var(--color-purple) !important;
			}
		}
	}

	&__button-call {
		position: relative;
		display: inline-block;
		color: var(--color-grey);
		transition: color var(--main-transition);

		&::before {
			position: absolute;
			bottom: -2px;
			left: 0;
			width: 100%;
			height: 2px;
			border-bottom: 1px dashed var(--color-grey);
			transition: opacity 0.2s ease-in-out;
			content: "";
		}

		&:hover {
			color: var(--color-purple);

			&::before {
				opacity: 0;
			}
		}

		&:focus {
			color: var(--color-purple);

			&::before {
				opacity: 0;
			}
		}
	}

	&__contacts {
		position: absolute;
		top: -26px;
		right: 0;
		width: 213px;
		padding: 32px;
		background-color: var(--color-darker-blue);

		._contacts-group {
			margin-bottom: 1rem;

			span {
				color: var(--color-grey);
				font-size: 1rem;
				line-height: 1.3;
			}

			&:nth-last-of-type(1) {
				margin-bottom: 0;
			}

			&:nth-of-type(2) {
				span {
					display: block;
				}
			}
		}
	}

	@include _1200 {
		&__contacts {
			padding-right: 24px;
		}
	}

	@include _992 {
		&__contacts {
			display: none;
		}
	}

	@include _480 {
		padding-top: 18px;
	}
}
