.games-slider {
	position: relative;
	margin-bottom: 202px;
	padding-top: 231px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;

	._layer {
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		user-select: none;
		pointer-events: none;
	}

	&__controls {
		position: absolute;

		&._left {
			top: 290px;
			left: 288px;
			width: 272px;
			height: 346px;
		}

		&._right {
			top: 140px;
			right: 173px;
			width: 331px;
			height: 352px;
		}
	}

	&__title {
		position: relative;
		z-index: 10;
		width: 100%;
		max-width: 514px;
		margin: 0 auto;
		margin-bottom: 40px;

		h2 {
			position: relative;
			z-index: 4;
			margin-bottom: 0;
			font-size: 55px;
			line-height: 1.2;
			text-align: center;
			text-transform: uppercase;

			span {
				position: relative;
				display: block;
				color: var(--color-purple);
				text-shadow: 0 1px 36px var(--color-purple);

				&::before {
					position: absolute;
					top: 50%;
					left: -58px;
					width: 129px;
					height: 49px;
					background-image: url(../img/title-line-game-left.webp);
					background-repeat: no-repeat;
					background-position: center;
					background-size: auto;
					transform: translateY(-50%);
					content: "";
				}

				&::after {
					position: absolute;
					top: 50%;
					right: -58px;
					width: 129px;
					height: 49px;
					background-image: url(../img/title-line-game-right.webp);
					background-repeat: no-repeat;
					background-position: center;
					background-size: auto;
					transform: translateY(-50%);
					content: "";
				}
			}
		}

		._layer {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: -1;
			width: 1128px;
			height: 649px;
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			transform: translate(-50%, -50%);
		}
	}

	&__item {
		width: 320px;
		cursor: pointer;

		&-top {
			position: relative;
			width: 100%;
			height: 413px;
			margin-bottom: 27px;
		}

		&-img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&-hover {
			position: absolute;
			top: 0;
			left: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 100%;
			background-color: rgba($color: #000, $alpha: 0.7);
			opacity: 0;
			transition: opacity 0.2s ease-in-out;
		}

		&-hover-text {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 190px;
			height: 190px;
			padding: 15px;
			font-size: rem(20px);
			line-height: 1;
			background-color: rgba($color: var(--color-white), $alpha: 0.7);
			border: 1px solid var(--color-white);
			border-radius: 100%;
			backdrop-filter: blur(9px);
		}

		&-heading {
			display: block;
			font-weight: 500;
			font-size: rem(32px);
			line-height: 1.4;
			text-align: center;
			text-transform: uppercase;
		}

		&-subheading {
			position: relative;
			z-index: 2;
			display: block;
			padding: 0 40px;
			padding-top: 8px;
			color: var(--color-grey);
			font-size: rem(20px);
			line-height: 1.4;

			&::before {
				position: absolute;
				top: 0;
				left: 0;
				z-index: -1;
				width: 100%;
				height: 100%;
				background-image: url(../img/games-slider-line.svg);
				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;
				content: "";
			}
		}

		&:hover {
			.games-slider__item-hover {
				opacity: 1;
			}
		}

		&:nth-of-type(2n) {
			transform: translateY(-57px);
		}
	}

	&__navigation {
		position: relative;
		z-index: 10;
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 216px;
		margin: 0 auto;
		margin-bottom: 96px;
	}

	@include _1680 {
		&__title {
			h2 {
				font-size: rem(50px);

				&::before {
					left: -18px;
					width: 109px;
					height: 79px;
					background-size: cover;
				}

				&::after {
					right: -18px;
					width: 109px;
					height: 79px;
					background-size: cover;
				}
			}
		}

		&__controls {
			&._right {
				top: 160px;
				right: 113px;
				width: 211px;
				height: 222px;
			}

			&._left {
				top: 230px;
				left: 98px;
				width: 212px;
				height: 266px;
			}
		}

		&__item-top {
			height: 383px;
		}
	}

	@include _1536 {
		padding-top: 160px;

		&__item-top {
			height: 333px;
			margin-bottom: 20px;
		}

		&__item-heading {
			font-size: 1.8rem;
		}

		&__item-subheading {
			font-size: 1rem;

			&::before {
				top: -3px;
			}
		}

		&__item-hover-text {
			width: 160px;
			height: 160px;
			font-size: 1rem;
		}
	}

	@include _1366 {
		padding-top: 140px;

		&__title {
			h2 {
				font-size: rem(45px);
			}
		}
	}

	@include _1200 {
		padding-top: 70px;

		&__title {
			max-width: 424px;

			._layer {
				width: 448px;
				height: 399px;
			}

			h2 {
				font-size: rem(35px);

				span {
					&::before,
					&::after {
						display: none;
					}
				}
			}
		}

		&__controls {
			&._left {
				top: 90px;
				left: 68px;
				width: 152px;
				height: 196px;
			}

			&._right {
				top: 130px;
				right: 43px;
				width: 151px;
				height: 162px;
			}
		}

		&__item-top {
			height: 393px;
		}
	}

	@include _769 {
		&__navigation {
			width: 166px
		}

		&__title {
			max-width: 374px;

			h2 {
				font-size: rem(30px);
			}
		}

		&__controls {
			&._left {
				top: 140px;
				left: 28px;
				width: 132px;
				height: 166px;
			}

			&._right {
				top: 120px;
				right: 23px;
				width: 131px;
				height: 142px;
			}
		}

		&__item-top {
			height: 343px;
		}

		&__item-headig {
			font-size: 1.4rem;
		}

		&__item-subheading {
			&::before {
				top: 0;
			}
		}
	}

	@include _740 {
		margin-bottom: 122px;

		&__item-top {
			height: 282px;
		}
	}

	@include _480 {
		&__controls {
			display: none;
		}

		&__title {
			max-width: 234px;
		}

		&__item {
			&:nth-of-type(2n) {
				transform: none;
			}

			&-top {
				height: 232px;
			}

			&-heading {
				font-size: 1.3rem;
			}
		}

		&__navigation {
			margin-bottom: 46px;
		}
	}

	@include _360 {
		&__title {
			margin-bottom: 20px;
		}

		&__item-top {
			height: 372px;
		}
	}
}
