.reviews {
	position: relative;

	._layer {
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		user-select: none;
		pointer-events: none;
	}

	&__title {
		max-width: 1650px;
		margin: 0 auto;
		margin-bottom: 34px;

		h2 {
			margin-bottom: 0;
			font-size: 55px;
			line-height: 1.2;
			text-align: center;
			text-transform: uppercase;
		}
	}

	&__slider-navigation {
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-width: 216px;
		margin: 0 auto;
		margin-bottom: 40px;
		margin-left: 53%;
	}

	&__millenniumFalcon {
		position: absolute;
		bottom: -130px;
		left: 0;
		z-index: 150;
		width: 892px;
		height: 488px;

		&-ship {
			position: absolute;
			right: 0;
			bottom: 0;
			z-index: 1;
			width: 819px;
			height: 377px;
		}

		&-light {
			position: absolute;
			top: 0;
			left: 0;
			width: 599px;
			height: 427px;
		}
	}

	@include _1680 {
		&__title {
			h2 {
				font-size: rem(50px);
			}
		}

		&__millenniumFalcon {
			width: 762px;
			height: 418px;
		}

		&__millenniumFalcon-ship {
			width: 709px;
			height: 297px;
		}

		&__millenniumFalcon-light {
			width: 499px;
			height: 387px;
		}
	}

	@include _1536 {
		&__slider-navigation {
			margin-left: 49%;
		}
	}

	@include _1366 {
		&__title {
			h2 {
				font-size: rem(45px);
			}
		}

		&__slider-navigation {
			margin-bottom: 35px;
		}

		&__millenniumFalcon {
			bottom: -10px;
			left: -40px;
		}
	}

	@include _1200 {
		&__title {
			h2 {
				font-size: rem(35px);
			}
		}

		&__slider-navigation {
			margin-right: auto;
			margin-left: auto;
		}

		&__millenniumFalcon {
			bottom: 60px;
			left: -70px;
			width: 582px;
			height: 318px;

			&-ship {
				width: 539px;
				height: 237px;
			}

			&-light {
				width: 399px;
				height: 287px;
			}
		}
	}

	@include _769 {
		&__slider-navigation {
			max-width: 166px;
		}
	}

	@include _740 {
		&__title {
			h2 {
				font-size: rem(30px);
			}
		}

		&__millenniumFalcon {
			width: 442px;
			height: 208px;

			&-ship {
				width: 399px;
				height: 167px;
			}

			&-light {
				width: 259px;
				height: 177px;
			}
		}
	}


	@include _480 {
		&__title {
			margin-bottom: 14px;
		}

		&__millenniumFalcon {
			left: -10px;
			bottom: 0px;
			width: 322px;
			height: 168px;

			&-ship {
				width: 299px;
				height: 134px;
			}

			&-light {
				width: 209px;
				height: 137px;
			}
		}
	}
}

.reviews-slider {
	&__slide {
		position: relative;
		width: 100%;
		padding-bottom: 473px;
	}

	&__big-image {
		position: absolute;

		&._first {
			top: -90px;
			left: 154px;
		}

		&._second {
			right: 406px;
			bottom: 0;
			z-index: 4;
		}

		&._third {
			right: 0;
			bottom: 218px;
		}

		&._fourth {
			bottom: 258px;
			left: 0;
		}
	}

	&__item {
		width: 524px;
		margin: 0 auto;
		margin-left: 40%;
		padding: 85px 30px 38px 30px;
		background-image: url(../img/slider-review-border.svg);
		background-repeat: no-repeat;
		background-position: 0 0;
		background-size: contain;
	}

	&__head {
		display: flex;
		margin-bottom: 20px;
	}

	&__logo {
		margin-right: 20px;
	}

	&__image {
		width: 40px;
		height: 40px;
		object-fit: cover;
		border-radius: 50%;
	}

	&__name {
		display: block;
		margin-bottom: 10px;
		font-weight: 200;
		font-size: rem(20px);
		line-height: 1.5;
	}

	&__text {
		margin-right: 20px;
		color: var(--color-grey);
		line-height: 30px;

		&:last-child {
			margin-right: 0;
		}
	}

	&__content {
		p {
			max-width: 465px;
			margin: 0;
			font-weight: 200;
			font-size: rem(20px);
			line-height: 1.5;
		}
	}

	@include _1680 {
		&__big-image {
			&._first {
				width: 420px;
				height: 570px;
				object-fit: cover;
			}

			&._second {
				right: 236px;
				width: 390px;
				height: 430px;
			}

			&._third {
				bottom: 428px;
				width: 398px;
				height: 508px;
			}

			&._fourth {
				bottom: 258px;
				width: 458px;
				height: 448px;
			}
		}
	}

	@include _1536 {
		&__big-image {
			&._first {
				left: 64px;
			}

			&._second {
				right: 236px;
				width: 390px;
				height: 430px;
			}

			&._third {
				bottom: 428px;
				width: 398px;
				height: 508px;
			}

			&._fourth {
				bottom: 288px;
			}
		}

		&__item {
			margin-left: 35%;
		}
	}

	@include _1440 {
		&__content {
			p {
				max-width: 385px;
				font-size: 1rem;
			}
		}

		&__slide {
			padding-bottom: 413px;
		}

		&__item {
			width: 444px;
			padding: 67px 30px 38px;
		}

		&__big-image {
			&._first {
				top: 0;
				left: 14px;
			}

			&._second {
				right: 156px;
			}

			&._fourth {
				display: none;
			}
		}
	}

	@include _1366 {
		&__big-image {
			&._first {
				width: 360px;
				height: 490px;
			}

			&._second {
				right: 206px;
				width: 330px;
				height: 350px;
			}

			&._third {
				bottom: 398px;
				width: 368px;
				height: 478px;
			}
		}

		&__item {
			margin-left: 33%;
		}
	}

	@include _1200 {
		&__big-image {
			&._first {
				display: none;
			}

			&._second {
				right: 30px;
				bottom: 50px;
			}

			&._third {
				top: 40px;
				right: 80px;
				bottom: auto;
			}
		}

		&__item {
			margin-left: 0;
		}
	}

	@include _769 {
		&__big-image {
			&._second {
				z-index: 6;
				right: -10px;
				bottom: 90px;
			}

			&._third {
				display: none;
			}
		}

		&__slide {
			padding-bottom: 403px;
		}

		&__item {
			position: relative;
			z-index: 4;
			width: 614px;
			margin-left: auto;
			margin-right: auto;
			background-image: none;
			background-color: var(--color-dark-blue);
			border: 0.5px solid var(--color-grey);
		}

		&__content {
			p {
				max-width: 90%;
			}
		}
	}

	@include _769 {
		&__big-image {
			&._second {
				display: none;
			}
		}

		&__item {
			width: 97%;
		}

		&__slide {
			padding-bottom: 263px;
		}
	}

	@include _480 {
		&__item {
			padding: 30px 15px;
		}

		&__slide {
			padding-bottom: 173px;
		}

		&__name {
			font-size: 1.15rem;
    		line-height: 1.2;
			margin-bottom: 0;
		}

		&__text {
			margin-right: 8px;
		}
	}
}
