.vr-room {
	position: relative;

	&__container {
		max-width: 1255px;
	}

	&__vr-lines {
		position: absolute;
		top: -290px;
		left: 50%;
		width: 482px;
		height: 1728px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: auto;
		transform: translateX(-50%);
	}

	&__title {
		position: relative;
		z-index: 20;
		max-width: 417px;
		margin-bottom: 32px;

		h2 {
			margin-bottom: 16px;
			font-size: 55px;
			line-height: 1.2;
			text-transform: uppercase;

			span {
				color: var(--color-light-blue);
				text-shadow: 0 1px 36px var(--color-light-blue);
			}
		}

		p {
			position: relative;
			max-width: 175px;
			margin: 0;
			margin-left: calc(100% - 205px);
			font-weight: 200;
			font-size: 24px;
			line-height: 1.2;

			&::before {
				position: absolute;
				top: 9px;
				left: -46px;
				width: 32px;
				height: 11px;
				background-image: url(../img/right-arrow.svg);
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				content: "";
			}
		}
	}

	&__slider-nav {
		position: relative;
		z-index: 150;
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-width: 202px;
	}

	@include _1680 {
		&__title {
			h2 {
				font-size: rem(50px);
			}
		}

		&__container {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
			max-width: 1155px;
		}

		&__vr-lines {
			height: 1238px;
			background-size: 112%;
		}

		&__slider-nav {
			width: 100%;
		}
	}

	@include _1536 {
		&__vr-lines {
			display: none;
		}

		&__container {
			max-width: 1177px;
		}
	}

	@include _1440 {
		&__container {
			max-width: 100%;
		}
	}

	@include _1366 {
		&__title {
			h2 {
				font-size: rem(45px);
			}

			p {
				margin-left: 46px;
				font-size: 1.2rem;

				&::before {
					top: 6px;
				}
			}
		}
	}

	@include _1200 {
		&__title {
			h2 {
				font-size: rem(35px);
			}

			p {
				max-width: 155px;
			}
		}

		&__container {
			align-items: flex-end;
		}

		&__slider-nav {
			margin-bottom: 32px;
		}
	}

	@include _992 {
		&__title {
			max-width: 487px;
		}

		&__slider-nav {
			max-width: 166px;
		}
	}

	@include _740 {
		&__title {
			h2 {
				font-size: rem(30px);
			}
		}

		&__container {
			flex-direction: column;
			align-items: stretch;
			justify-content: stretch;
		}
	}

	@include _360 {
		&__title {
			h2 {
				font-size: 1.9rem;
			}
		}
	}
}

.vr-room-slider {
	margin-top: -608px;

	&__item {
		position: relative;
		z-index: 5;
		width: 100%;
		height: 1225px;
		background-repeat: no-repeat;
		background-position: center bottom;
		background-size: contain;
	}

	&__info {
		position: absolute;
		top: 394px;
		right: 48px;
		z-index: 10;
		max-width: 328px;
		padding: 40px 86px 37px 29px;
		color: var(--color-white);
		font-size: 18px;
		line-height: 1.4;
		background-color: rgba($color: #1f2934, $alpha: 0.4);
		border: 1px solid var(--color-purple);
		// clip-path: polygon(0% 0%, 85% 0%, 100% 40%, 100% 100%, 0% 100%);

		&-plus {
			position: absolute;
			bottom: -54px;
			left: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 50px;
			height: 50px;
			background-color: rgba($color: #1f2934, $alpha: 0.4);
			border: 1px solid var(--color-purple);

			._span {
				position: absolute;
				top: 50%;
				left: 50%;
				display: block;
				width: 18px;
				height: 1px;
				background-color: var(--color-purple);
				transform: translate(-50%, -50%);

				&:nth-of-type(1) {
					transform: translate(-50%, -50%) rotate(90deg);
				}
			}
		}
	}

	@include _1680 {
		margin-top: -405px;

		&__item {
			height: 870px;
			background-position: center top;
			background-size: cover;
		}
	}

	@include _1440 {
		margin-top: -362px;

		&__item {
			height: 800px;
		}
	}

	@include _1366 {
		&__info {
			top: 292px;
		}

		&__item {
			height: 740px;
		}
	}

	@include _1200 {
		margin-top: -160px;

		&__info {
			top: 200px;
			right: auto;
			left: 24px;
		}
	}

	@include _992 {
		margin-top: -120px;

		&__item {
			height: 610px;
		}

		&__info {
			padding: 20px 49px 37px 22px;
		}
	}

	@include _740 {
		&__info {
			max-width: 298px;
		}
	}

	@include _480 {
		&__info {
			padding: 20px 20px 27px 22px;
			font-size: 1rem;
		}
	}

	@include _320 {
		margin-top: -60px;

		&__item {
			height: 440px;
		}

		&__info {
			max-width: 238px;
			left: 16px;
		}
	}
}
