@import "mixins/media.scss";
@import "func/px-to-rem.scss";
@import "settings/setttings.scss";

.site-logo {
	display: flex;
	align-items: center;

	&__logo {
		display: block;
		max-width: 125px;
		margin-right: 1rem;

		@include _480 {
			max-width: 110px;
		}

		@include _375 {
			max-width: 100px;
		}

		@include _320 {
			max-width: 90px;
		}
	}

	&__text {
		max-width: 162px;
		color: var(--color-white);
		transition: color var(--main-transition);

		@include _1280 {
			font-size: 14px;
		}

		@include _575 {
			display: none;
		}
	}

	&:focus {
		.site-logo__text {
			color: var(--color-purple);
		}
	}

	&:hover {
		.site-logo__text {
			color: var(--color-purple);
		}
	}
}

.phone-content {
	display: inline-block !important;
	color: var(--color-white) !important;
	font-weight: 500 !important;
	font-size: rem(18px) !important;
	line-height: 1.3;
	transition: color var(--main-transition) !important;
}

.btn {
	display: block;

	span {
		display: block;
		padding: 14px 24px;
		color: var(--color-purple);
		font-weight: 400;
		font-size: 1rem;
		text-transform: uppercase;
		border: 1px solid var(--color-purple);
		border-radius: 5px;
		transition: background-color var(--main-transition), color var(--main-transition), border-color var(--main-transition);

		@include _480 {
			padding: 13px 20px;
		}
	}

	&:hover {
		span {
			color: var(--color-white);
			background-color: var(--color-light-blue);
			border-color: var(--color-light-blue);
		}
	}

	&:focus {
		span {
			color: var(--color-white);
			background-color: var(--color-light-blue);
			border-color: var(--color-light-blue);
		}
	}
}

.button-secondary {
	position: relative;
	z-index: 5;
	padding: 33px 47px 32px 33px;
	color: var(--color-purple);
	font-size: 16px;
	line-height: 23px;
	text-transform: uppercase;

	&__icon {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		fill: var(--color-purple);
	}

	&__line {
		position: absolute;
		top: 0;
		width: 20px;
		height: 86px;
		fill: var(--color-purple);

		&._first {
			right: -20px;
		}

		&._second {
			right: -60px;
		}
	}

	span {
		color: #74d514;
		transition: color var(--main-transition);
	}

	&::before {
		position: absolute;
		top: 0;
		right: -23px;
		width: 17px;
		height: 86px;
		background-image: url(../img/button-lines.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		content: "";
	}

	&::after {
		position: absolute;
		top: 0;
		right: -46px;
		width: 17px;
		height: 86px;
		background-image: url(../img/button-lines.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		content: "";
	}

	&:focus {
		color: var(--color-white);

		&::before {
			animation: buttonLine 1s ease-in-out infinite;
		}

		&::after {
			animation: buttonLine 1s ease-in-out infinite;
		}
	}

	&:active {
		transform: scale(0.99);
	}

	&:hover {
		color: var(--color-white);

		&::before {
			animation: buttonLine 1s ease-in-out infinite;
		}

		&::after {
			animation: buttonLine 1s ease-in-out infinite;
		}
	}
}

@keyframes buttonLine {
	0% {
		transform: translateX(0);
		opacity: 1;
	}

	100% {
		transform: translateX(15px);
		opacity: 0;
	}
}

.slider-navigation {
	&__nav-icon {
		width: 31px;
		height: 11px;
		pointer-events: none;
		fill: var(--color-purple);
	}

	&__nav-button {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 92px;
		height: 92px;
		border: 1px solid var(--color-purple);
		border-radius: 100%;
		cursor: pointer;
		transition: background-color var(--main-transition);

		&:hover {
			background-color: var(--color-purple);

			.slider-navigation__nav-icon {
				fill: var(--color-white);
			}
		}

		&:focus {
			background-color: var(--color-purple);

			.slider-navigation__nav-icon {
				fill: var(--color-white);
			}
		}

		&:active {
			background-color: #70298b;
			border-color: #70298b;
		}

		@include _992 {
			width: 72px;
			height: 72px;
		}
	}
}

.site-label {
	position: relative;
	display: block;
	width: 100%;
}

.site-input {
	width: 100%;
	height: 84px;
	padding: 32px 35px;
	color: var(--color-white);
	font-size: rem(14px);
	line-height: 1.2;
	background: linear-gradient(180deg, rgba(31, 0, 47, 1) 10%, rgba(73, 0, 51, 1) 57%, rgba(116, 0, 64, 1) 100%);
	border: 1px solid var(--color-white);

	&::placeholder {
		color: var(--color-white);
		font-size: rem(14px);
	}

	&:focus {
		outline: none;
	}

	@include _1366 {
		height: 74px;
		padding: 22px 25px;
	}
}

.site-agreement {
	position: relative;
	display: block;
	max-width: 354px;
	margin: 0 auto;
	cursor: pointer;

	&__text {
		position: relative;
		display: block;
		padding-left: 36px;
		font-size: 12px;
		line-height: 18px;

		&::before {
			position: absolute;
			top: 0;
			left: 0;
			width: 24px;
			height: 24px;
			border: 1px solid var(--color-white);
			border-radius: 2px;
			content: "";
		}

		&::after {
			position: absolute;
			top: 5px;
			left: 3px;
			width: 19px;
			height: 14px;
			background-image: url(../img/tick.svg);
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			opacity: 0;
			content: "";
		}
	}

	&__link {
		&:hover {
			color: var(--color-purple);
		}
	}

	&__input:checked {
		~ .site-agreement__text {
			&::after {
				opacity: 1;
			}
		}
	}
}

.third-button {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 32px;
	color: var(--color-third-button);
	font-size: rem(18px);
	line-height: 28px;
	background-color: var(--color-light-blue);
	transition: background-color var(--main-transition), color var(--main-transition);

	&__icon {
		display: block;
		width: 10px;
		height: 26px;
		margin-left: 40px;
		transition: fill var(--main-transition);
		fill: var(--color-third-button);
	}

	&:hover {
		color: var(--color-white);
		background-color: var(--color-purple);

		.third-button__icon {
			fill: var(--color-white);
		}
	}

	&:focus {
		color: var(--color-white);
		background-color: var(--color-purple);

		.third-button__icon {
			fill: var(--color-white);
		}
	}
}

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1000;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	padding-top: 86px;
	background-color: var(--color-dark-blue);
	// background-image: url(../img/preloader.webp);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	transition: opacity var(--main-transition);

	@include _1536 {
		padding-top: 56px;
	}

	@include _992 {
		padding-top: 156px;
	}

	@include _740 {
		padding-top: 100px;
	}

	@include _320 {
		padding-top: 70px;
	}

	&__text {
		position: absolute;
		bottom: 30px;
		right: 30px;
		font-size: 160px;
		line-height: 1;
		font-family: $second-font-family;

		@include _1536 {
			font-size: 120px;
		}

		@include _992 {
			display: none;
		}
	}

	&__logo {
		margin-bottom: 21px;

		@include _1536 {
			width: 360px;
		}

		@include _575 {
			width: 230px;
		}
	}

	&__loader {
		max-width: 540px;
		width: 100%;
		text-align: center;

		@include _1536 {
			max-width: 480px;
		}

		@include _500 {
			max-width: 75%;
		}

		@include _480 {
			max-width: 85%;
		}
	}

	&__lodaer-name {
		display: block;
		width: 135px;
		height: 23px;
		margin: 0 auto;
		margin-bottom: 30px;
		background-image: url(../img/loading.webp);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
	}

	&__loader-line {
		display: flex;
		align-items: center;
		padding: 0 5px;
		width: 100%;
		height: 43px;
		border: 2px solid;
		border-image-slice: 1;
		border-width: 2px;
		border-image-source: linear-gradient(to left, var(--color-purple), var(--color-light-blue));
		box-shadow: 0 0 76px var(--color-purple);
	}

	&__loader-gradient-line {
		position: relative;
		display: block;
		width: 0;
		height: 34px;
		overflow: hidden;

	}

	&__loader-image {
		position: absolute;
		top: 0;
		left: 0;
		width: 538px;
		height: 100%;
		background-image: url(../img/gradient-color.webp);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;

		@include _575 {
			width: 468px;
		}

		@include _500 {
			width: 358px;
		}
	}

	.preloader-headfones {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 1343px;
		height: 454px;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;

		@include _1680 {
			width: 833px;
			height: 284px;
		}

		@include _1280 {
			width: 563px;
			height: 194px;
		}

		@include _1200 {
			left: -90px;
		}

		@include _992 {
			display: none;
		}
	}
}
