*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	box-sizing: border-box;
	font-size: 16px;

	@include _480 {
		font-size: 14px;
	}

	@include _320 {
		font-size: 13px;
	}
}

body {
	color: var(--color-white);
	font-family: $main-font-family;
	background-color: var(--color-dark-blue);

	@include _2100 {
		zoom: 1.2;
	}
}

img {
	max-width: 100%;
	height: auto;
	vertical-align: middle;
}

.fontLoaded h2 {
	font-family: $second-font-family;
}

.fontLoaded .services__heading-line._line-first ._span  {
	font-family: $second-font-family;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin-top: 0;
}

a {
	color: inherit;
	text-decoration: none;
}

ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

a:focus {
	outline: none !important;
}

button:focus {
	outline: none !important;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	clip: rect(0 0 0 0);
}

.site-container {
	width: 100%;
	margin: 0 auto;
	padding: 0 40px;

	@include _1440 {
		padding-right: 32px;
		padding-left: 32px;
	}

	@include _1200 {
		padding-right: 24px;
		padding-left: 24px;
	}

	@include _500 {
		padding-right: 20px;
		padding-left: 20px;
	}

	@include _320 {
		padding-right: 16px;
		padding-left: 16px;
	}
}

.page-wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	overflow: hidden;
}

.page-main {
	flex-grow: 1;
}

.button-reset {
	margin: 0;
	padding: 0;
	background-color: transparent;
	border: 0;
	border-radius: 0;
	cursor: pointer;
}

.section {
	padding: 0;
}
